import React, { Component } from 'react'
import ImageUploader from 'react-images-upload'
import headerIndicator from './template/header_indicator.png'
import headerLogo from './template/header_logo.png'
import textBackground from './template/text_background.png'
import './App.css'
import { Card, Typography, Slider, Button, Input } from 'antd'
import { createCanvas, loadImage } from 'canvas'

const { Text } = Typography
const { TextArea } = Input

// Canvas constants
const COVER_WIDTH = 900
const COVER_HEIGHT = 478

// Text constants
const DEFAULT_TEXT_BACKGROUND_MARGIN_BOTTOM = 17
const DEFAULT_TEXT_MARGIN_TOP_DOUBLE_LINE = 55
const DEFAULT_TEXT_MARGIN_TOP_SINGLE_LINE = 70
const DEFAULT_TEXT_LINE_SPACING = 30
const DEFAULT_TEXT_MARGIN_LEFT = 40
const FONT_SIZE = '25px'
const MAX_TEXT_COVER = 50

// Shadow constants
const DEFAULT_SHADOW_OPACITY = 30

// Export constant
const DEFAULT_IMAGE_QUALITY = 100
const DEFAULT_FILE_NAME = 'cover.jpg'

// Image resize calculation
const FIT_BOTH = 0
const FIT_WIDTH = 1
const FIT_HEIGHT = 2

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      originalDataUrl: undefined,
      coverDataUrl: undefined,
      text1: undefined,
      text2: undefined,
      isImageSelected: false,
      shadowOpacity: DEFAULT_SHADOW_OPACITY,
      imageQuality: DEFAULT_IMAGE_QUALITY
    }
  }

  calculateImageSize = (imageWidth, imageHeight, canvasWidth, canvasHeight) => {
    let side = this.getMostSizeFit(imageWidth, imageHeight, canvasWidth, canvasHeight)
    let width = 100
    let height = 100
    if(side === FIT_HEIGHT) {
      width = canvasHeight * imageWidth / imageHeight
      height = canvasHeight
    } else if(side === FIT_WIDTH) {
      width = canvasWidth
      height = canvasWidth * imageHeight / imageWidth
    } else {
      width = canvasWidth
      height = canvasHeight
    }
    return { width, height}
  }

  getMostSizeFit = (imageWidth, imageHeight, canvasWidth, canvasHeight) => {
    let imageRatio = imageWidth / imageHeight
    let canvasRatio = canvasWidth / canvasHeight
    if (canvasRatio > imageRatio) {
      return FIT_WIDTH
    } else if(canvasRatio < imageRatio) { 
      return FIT_HEIGHT
    } else {
      return FIT_BOTH
    }
  }

  generateCover = async () => {
    const canvas = createCanvas(COVER_WIDTH, COVER_HEIGHT)
    const context = canvas.getContext('2d')
    const { originalDataUrl, text1, text2, shadowOpacity, imageQuality } = this.state

    // Draw background
    let backgroundImage = await loadImage(originalDataUrl)
    let { width: backgroundImageWidth, height: backgroundImageHeight } = this.calculateImageSize(backgroundImage.width, backgroundImage.height, canvas.width, canvas.height)
    let backgroundImagePositionX = (canvas.width - backgroundImageWidth) / 2
    let backgroundImagePositionY = (canvas.height - backgroundImageHeight) / 2
    context.drawImage(backgroundImage, backgroundImagePositionX, backgroundImagePositionY, backgroundImageWidth, backgroundImageHeight)

    // Draw shadow
    context.fillStyle = 'rgba(0, 0, 0, ' + shadowOpacity / 100 + ')'
    context.fillRect(0, 0, canvas.width, canvas.height)

    // Draw header indicator and logo
    context.drawImage(await loadImage(headerIndicator), 0, 55)
    context.drawImage(await loadImage(headerLogo), 25, 46)

    // Draw text background
    let textBackgroundImage = await loadImage(textBackground)
    context.drawImage(textBackgroundImage, canvas.width - textBackgroundImage.width, (canvas.height - textBackgroundImage.height) - DEFAULT_TEXT_BACKGROUND_MARGIN_BOTTOM)
    
    // Calculate the position and margin of text
    let textMarginTop = text2 ? DEFAULT_TEXT_MARGIN_TOP_DOUBLE_LINE : DEFAULT_TEXT_MARGIN_TOP_SINGLE_LINE 
    let text1PositionX = canvas.width - textBackgroundImage.width + DEFAULT_TEXT_MARGIN_LEFT
    let text2PositionX = text1PositionX
    let text1PositionY = ((canvas.height - textBackgroundImage.height) - DEFAULT_TEXT_BACKGROUND_MARGIN_BOTTOM) + textMarginTop
    let text2PositionY = text1PositionY + DEFAULT_TEXT_LINE_SPACING

    // Draw foreground 1st cover text
    const title1 = text1 || ''
    context.font = FONT_SIZE + ' "Sarabun"'
    context.fillStyle = 'white'
    context.fillText(title1, text1PositionX, text1PositionY)

    // Draw foreground 2nd cover text
    const title2 = text2 || ''
    context.font = FONT_SIZE + ' "Sarabun"'
    context.fillStyle = 'white'
    context.imageSmoothingEnabled = true
    context.fillText(title2, text2PositionX, text2PositionY)

    // Update to state
    this.setState({
      coverDataUrl: canvas.toDataURL('image/jpeg', imageQuality / 100)
    })
  }

  loadImage = source => {
    return new Promise((resolve, reject) => {
      let image = new Image()
      image.src = source
      image.onload = () => {
        resolve(image)
      }
    })
  }

  onImageSelected = (files, dataUrls) => {
    if (dataUrls.length > 0 && dataUrls[0] !== undefined) {
      this.setState({
        originalDataUrl: dataUrls[0],
        isImageSelected: true
      })
      this.refresh()
    }
  }

  onTextChanged = e => {
    let words = e.target.value.split('\n')
    words = words
      .map(word => {
        return word.slice(0, MAX_TEXT_COVER)
      })
      .slice(0, 2)
    e.target.value = words.join('\n')
    this.setState({
      text1: words[0],
      text2: words[1]
    })
    this.refresh()
  }

  onShadowOpacityChanged = value => {
    this.setState({
      shadowOpacity: value
    })
    this.refresh()
  }

  onImageQualityChanged = value => {
    this.setState({
      imageQuality: value
    })
    this.refresh()
  }

  refresh = () => {
    setTimeout(() => {
      this.generateCover()
    }, 300)
  }

  downloadCover = () => {
    let a = document.createElement('a')
    a.href = this.state.coverDataUrl
    a.download = DEFAULT_FILE_NAME
    a.click()
  }

  backToMain = () => {
    this.setState({
      originalDataUrl: undefined,
      coverDataUrl: undefined,
      text1: undefined,
      text2: undefined,
      isImageSelected: false, 
      shadowOpacity: DEFAULT_SHADOW_OPACITY,
      imageQuality: DEFAULT_IMAGE_QUALITY
    })
  }

  render() {
    const { coverDataUrl, isImageSelected } = this.state
    return (
      <div className="App">
        {isImageSelected ? (
          <Card className="cover-generator">
            <div className="cover-generator-header">
              <Button className="back-button" icon="arrow-left" size="default" onClick={this.backToMain}>
                Back
              </Button>
            </div>
            <div>
              <img className="image-render" src={coverDataUrl} alt="" onClick={this.downloadCover} />
            </div>
            <div>
              <Text className="download-description">Click on the image to download</Text>
            </div>
            <div className="cover-text">
              <TextArea
                className="cover-text-title"
                placeholder="Text on cover image"
                autosize={{ minRows: 1, maxRows: 2 }}
                onChange={this.onTextChanged}
              />
            </div>
            <div className="shadow-opacity">
              <Text className="shadow-opacity-label">Shadow</Text>
              <Slider
                className="shadow-opacity-slider"
                defaultValue={DEFAULT_SHADOW_OPACITY}
                min={0}
                max={100}
                step={10}
                dots={true}
                onChange={this.onShadowOpacityChanged}
              />
            </div>
            <div className="image-quality">
              <Text className="image-quality-label">Image Quality</Text>
              <Slider
                className="image-quality-slider"
                defaultValue={DEFAULT_IMAGE_QUALITY}
                min={50}
                max={100}
                step={10}
                dots={true}
                onChange={this.onImageQualityChanged}
              />
            </div>
          </Card>
        ) : (
          <ImageUploader
            className="upload-button"
            withIcon={true}
            singleImage={true}
            buttonText="Choose image"
            label="JPG, JPEG, PNG (Maximum 10MB)"
            accept="image/png, image/jpeg"
            imgExtension={['.jpg', '.jpeg', '.png']}
            maxFileSize={10485760}
            onChange={this.onImageSelected}
          />
        )}
      </div>
    )
  }
}

export default App
